import useDebouncedCallback from "../../hooks/useDebouncedCallback";
import useItems from "../../store/items.store";
import DeleteItemButton from "../DeleteItemButton";
import TextItem from "./TextItem";

interface Props {
    nodeContent: string;
    header: string | undefined;
}

const BasicNode = (props: Props) => {
    const { nodeContent, header } = props;
    const { removeItem } = useItems();
    const handleClick = useDebouncedCallback(removeItem, [header, nodeContent]);

    return (
        <div className="node" id={nodeContent}>
            <TextItem str={nodeContent} />
            <DeleteItemButton nodeText={nodeContent} handleClick={handleClick} />
        </div>
    );
};

export default BasicNode;
