import React from "react";
import _ from "lodash";

import AppTitle from "./MainTree/AppTitle";
import AppendItemModal from "./AppendItemModal";
import MainTree from "./MainTree";
import useItems from "../store/items.store";
import useInput from "../store/input.store";

export const App: React.FC = (): JSX.Element => {
    const { items } = useItems();
    const {
        input: { isAppendItemModalVisible },
    } = useInput();
    const content = items ? _.cloneDeep(items) : null; // todo przydałby się jakiś selector żeby tak nie robić, w każdym razie bez tego jest błąd

    const header = content ? (content.shift() as string) : undefined;

    return items && items.length ? (
        <main>
            <AppTitle />
            <AppendItemModal renderCondition={isAppendItemModalVisible} />
            <MainTree content={content} header={header} />
        </main>
    ) : null;
};

export default App;
