import uuid from "react-uuid";
import AppendItemButton from "../AppendItemButton";
import BranchRootNode from "./BranchRootNode";
import EnhancedNode from "./EnhancedElement";

interface Props {
    nodeContent: string[];
}

export const BranchNode = (props: Props) => {
    const { nodeContent } = props;
    const header = nodeContent.shift();
    if (!header) return null;
    return (
        <>
            <BranchRootNode string={header} />
            <div className={"contentWrapperSecondary"}>
                {nodeContent.map(item => (
                    <EnhancedNode key={uuid()} nodeContent={item} header={header} />
                ))}
                <AppendItemButton str={header} primary={false} />
            </div>
        </>
    );
};

export default BranchNode;
